@import 'colors';

:local(.gallery) {
    text-align: center;
    color: $white;
    background-color: $item;
    border-radius: 15px;
    padding: 4%;
    margin: 4% 0;

    h2 {
        margin: 0 0 4%;
    }

    p {
        margin: 4% 0 0;
    }

    img {
        max-width: 100%;
    }

}




